/* .socialmedia { This is located in the _social_nav.scss component} */
/*  Grid Layout  */
/*  .footstep, */
.context,
.socialnav,
.join {
  width: 100%;
}

@media all and (min-width: 600px) {
  .grid-wrapper {
    display: grid;
    grid-template-columns: 17em 1fr;
    grid-template-rows: 2.5em auto;
    grid-gap: 0.5em;
  }
  /*  .footstep { 
      grid-column-start: 1;
      grid-column-end: 4;
      grid-row-start: 1;
     grid-row-end: 2;
     }
  */
  .context {
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 1;
    grid-row-end: 3;
  }
  .socialnav {
    grid-column-start: 2;
    grid-column-end: 3;
    grid-row-start: 1;
    grid-row-end: 2;
  }
  .join {
    grid-column-start: 2;
    grid-column-end: 3;
    grid-row-start: 2;
    grid-row-end: 3;
    margin-top: 0.5em;
  }
}
@media all and (min-width: 800px) {
  .grid-wrapper {
    display: grid;
    grid-template-columns: 17em 1fr minmax(12em, 24%);
    grid-template-rows: 2.5em auto;
  }
  .socialnav {
    grid-column-start: 2;
    grid-column-end: 3;
    grid-row-start: 1;
    grid-row-end: 2;
  }
  .join {
    grid-column-start: 3;
    grid-column-end: 4;
    grid-row-start: 1;
    grid-row-end: 2;
    margin-top: 0;
  }
}
